import React, { useState } from 'react';
import './Login.css'; // Estilos para el login
import { Link } from 'react-router-dom'; // Importar para usar enlaces de navegación

function Login({ onLogin }) {
  const [username, setUsername] = useState('');
  const [password, setPassword] = useState('');

  const handleSubmit = (e) => {
    e.preventDefault();
    onLogin(username, password); 
    localStorage.setItem('username', username); // Guardar el nombre de usuario en localStorage
  };

  return (
    <div className="login-container">
      <h2>Iniciar sesión</h2>
      <form onSubmit={handleSubmit}>
        <div>
          <label htmlFor="username">Usuario:</label>
          <input
            type="text"
            id="username"
            value={username}
            onChange={(e) => setUsername(e.target.value)}
            placeholder="Introduce tu usuario"
            required
          />
        </div>
        <div>
          <label htmlFor="password">Contraseña:</label>
          <input
            type="password"
            id="password"
            value={password}
            onChange={(e) => setPassword(e.target.value)}
            placeholder="Introduce tu contraseña"
            required
          />
        </div>
        <button type="submit" className="login-button">Entrar</button>
      </form>

      <div className="register-link-container">
        <p>¿No tienes una cuenta?</p>
        <Link to="#" className="register-link">Regístrate</Link> 
      </div>
    </div>
  );
}

export default Login;
